import React, { Component } from 'react'

import CSSModules from 'react-css-modules'
import styles from './style.css'

class Loading extends Component {
  render() {
    return (
      <div className="inline" style={this.props.style}>
        <div styleName="loading">
          <div>
            <div styleName="c1"></div>
            <div styleName="c2"></div>
            <div styleName="c3"></div>
            <div styleName="c4"></div>
          </div>
          <span className="fs14 text-center">Loading</span>
        </div>
      </div>
    )
  }
}

Loading.propTypes = {
  style: React.PropTypes.object
}

Loading.defaultProps = {
  style: {}
}

export default CSSModules(Loading, styles)



